.container{
  width: 100%;
}

@media (min-width: 640px){
  .container{
    max-width: 640px;
  }
}

@media (min-width: 768px){
  .container{
    max-width: 768px;
  }
}

@media (min-width: 1024px){
  .container{
    max-width: 1024px;
  }
}

@media (min-width: 1280px){
  .container{
    max-width: 1280px;
  }
}

.flex{
  display: flex;
}

.grid{
  display: grid;
}

.flex-col{
  flex-direction: column;
}

.justify-start{
  justify-content: flex-start;
}

.font-body{
  font-family: Poppins, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.font-heading{
  font-family: PT Serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.font-normal{
  font-weight: 400;
}

.font-semibold{
  font-weight: 600;
}

.font-bold{
  font-weight: 700;
}

.text-sm{
  font-size: 0.875rem;
}

.text-lg{
  font-size: 1.125rem;
}

.text-3xl{
  font-size: 1.875rem;
}

.my-2{
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.mx-auto{
  margin-left: auto;
  margin-right: auto;
}

.mt-6{
  margin-top: 1.5rem;
}

.mt-16{
  margin-top: 4rem;
}

.mt-20{
  margin-top: 5rem;
}

.mb-20{
  margin-bottom: 5rem;
}

.min-h-screen{
  min-height: 100vh;
}

.px-8{
  padding-left: 2rem;
  padding-right: 2rem;
}

.pb-10{
  padding-bottom: 2.5rem;
}

.relative{
  position: relative;
}

.text-center{
  text-align: center;
}

.text-gray-700{
  --text-opacity: 1;
  color: #4a5568;
  color: rgba(74, 85, 104, var(--text-opacity));
}

.text-opacity-50{
  --text-opacity: 0.5;
}

.uppercase{
  text-transform: uppercase;
}

.tracking-tight{
  letter-spacing: -0.025em;
}

.gap-12{
  grid-gap: 3rem;
  gap: 3rem;
}

.grid-cols-2{
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.ease-in{
  transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
}

.duration-300{
  transition-duration: 300ms;
}

@-webkit-keyframes spin{
  to{
    transform: rotate(360deg);
  }
}

@keyframes spin{
  to{
    transform: rotate(360deg);
  }
}

@-webkit-keyframes ping{
  75%, 100%{
    transform: scale(2);
    opacity: 0;
  }
}

@keyframes ping{
  75%, 100%{
    transform: scale(2);
    opacity: 0;
  }
}

@-webkit-keyframes pulse{
  50%{
    opacity: .5;
  }
}

@keyframes pulse{
  50%{
    opacity: .5;
  }
}

@-webkit-keyframes bounce{
  0%, 100%{
    transform: translateY(-25%);
    -webkit-animation-timing-function: cubic-bezier(0.8,0,1,1);
            animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50%{
    transform: none;
    -webkit-animation-timing-function: cubic-bezier(0,0,0.2,1);
            animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

@keyframes bounce{
  0%, 100%{
    transform: translateY(-25%);
    -webkit-animation-timing-function: cubic-bezier(0.8,0,1,1);
            animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50%{
    transform: none;
    -webkit-animation-timing-function: cubic-bezier(0,0,0.2,1);
            animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

.position {
  -ms-writing-mode: tb-rl;
      writing-mode: vertical-rl;
  margin-left: 0.5rem;
  font-size: 0.75rem;
  text-transform: uppercase;
  font-family: PT Serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-weight: 400;
  letter-spacing: -0.025em;
}

.photo-badge {
  font-weight: 600;
  font-size: 0.875rem;
  font-family: Poppins, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  margin-top: 0.5rem;
}

.sticky-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

.transition-width {
  transition-property: width;
}

.transition-max-width {
  transition-property: max-width;
}

.grid-item:nth-child(3n + 2) {
  margin-top: 0;
}

.grid-item:nth-child(2n + 2) {
  margin-top: 3rem;
}

@media (min-width: 1024px) {
  .grid-item:nth-child(2n + 2) {
    margin-top: 0;
  }

  .grid-item:nth-child(3n + 2) {
    margin-top: 8rem;
  }
}

